.Events {
  // margin: 2.5vh auto 10vh;
  // padding: 0 8vw;
  // width: 100% !important;

  // What We Do section
  .about-what-we-do {
    background-color: #f8eeff;

    .about-what-we-do-container {
      margin: 0 auto;
      padding: 3rem 0 1rem;

      .about-what-we-do-beginning {
        padding-left: 8vw;
        padding-right: 8vw;
        margin-bottom: 2rem;

        h2 {
          font-size: 2.5rem;
          font-weight: bold;
        }
      }

      .about-activities-container {
        padding: 0 6.25vw;
      }
    }

    // 700px/43.75rem and above (Vertical tablets and large horizontal phones)
    // 10vw of padding for width on large horizontal phones and tablets
    @media screen and (min-width: 43.75rem) {
      margin: 0 auto 3rem;

      .about-what-we-do-container {
        .about-what-we-do-beginning {
          padding-left: 6.25vw;
          padding-right: 6.25vw;

          max-width: 52rem;
          margin: 0 auto 4rem;
          text-align: center;

          h2 {
            font-size: 3rem;
          }
        }

        .about-activities-container {
          padding: 0;
        }
      }
    }

    // 1024px (horizontal tablets)
    @media screen and (min-width: 64rem) {
      .about-what-we-do-container {
        .about-what-we-do-beginning {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }

    // 1366px (medium laptops)
    // Increase margin on top and set max width for all larger resolutions
    @media screen and (min-width: 85.375rem) {
      margin: 0 auto;

      .about-what-we-do-container {
        padding: 3rem 0 1rem;
        padding-left: 0;
        padding-right: 0;
        // max-width: var(--large-screen-max-width);
      }
    }
  }
}
